.player-card {
  width: 50%;
  height: 100vh;
  /* Background pattern from Toptal Subtle Patterns */
  background-image: url(data:image/png;base64,);
  background-blend-mode: multiply;
  float: left;
  box-sizing: border-box;
  text-align: center;
  position: relative;
}

.player-card__indicator {
  position: absolute;
  top: 50%;
  width: 5%;
  height: 18%;
  background-color: rgba(0, 0, 0, 0.788);
  -webkit-transform: translate(0%, -50%);
          transform: translate(0%, -50%);
  z-index: 2000;
}
.player-card__indicator--1 {
  right: 0;
  -webkit-clip-path: polygon(100% 0, 0 50%, 100% 100%);
          clip-path: polygon(100% 0, 0 50%, 100% 100%);
}
.player-card__indicator--2 {
  left: 0;
  -webkit-clip-path: polygon(0 0, 100% 50%, 0 100%);
          clip-path: polygon(0 0, 100% 50%, 0 100%);
}

.player-card h3 {
  font-size: 35px;
  letter-spacing: 3px;
  font-weight: 100;
  text-transform: uppercase;
}

.player-card__score {
  font-size: 25px;
  font-weight: 100;
  margin: 0 auto;
}

.player-card__content {
  width: 90%;
}

.player-card__buttons {
  position: absolute;
  bottom: 4%;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  width: 70%;
}

.clearfix::after {
  content: "";
  height: 0;
  display: table;
  clear: both;
}

.btn {
  text-align: center;
  background-color: rgba(0, 0, 0, 0.788);
  color: white;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 7px;
}
.btn:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  background-color: rgba(119, 40, 72, 0.322);
}
.btn:active {
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  background-color: rgba(119, 40, 72, 0.322);
}

.btn-sel {
  font-size: 21px;
  padding: 2px;
  width: 70%;
  display: block;
  background-color: transparent;
  border: 2px solid black;
  outline: none;
  margin: 6% auto 0 auto;
}
.btn-play {
  font-size: 37px;
  padding: 10px 10px 0 10px;
  margin: 6px;
}
.btn-circle {
  font-size: 30px;
  padding: 8px 8px 0px 8px;
  margin: 20% auto;
  border-radius: 22px;
  background-color: rgba(119, 40, 72, 0.493);
  display: block;
}

.btn-set {
  font-size: 21px;
  font-weight: 100;
  padding: 8px;
  display: block;
  margin: 5px auto 15px auto;
}

.btn-check {
  margin-top: 60px;
  color: black;
}
.btn-check__label {
  font-family: "Lato", sans-serif;
  font-size: 13px;
}

